body {
    margin: 0;
    padding: 1em;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1, h2, h3 {
    margin: auto;
    text-align: center;
    padding-bottom: 1ch;
}

.shadow {
    background: #00000050;
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
}

.modal {
    width: 40ch;
    padding: 2em 1em;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1em;
    border-radius: .5em;
    background: white;
}

.tag {
    background: lightgreen;
    padding: .2em .5em;
    margin: .3em;
    border-radius: .3em;
}

